.flex-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.trans {
  -webkit-animation: trans 0.3s;
          animation: trans 0.3s;
}

@-webkit-keyframes trans {
  from {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes trans {
  from {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
.item-container {
  max-width: 760px;
  overflow: hidden;
  margin: 0 auto;
}

body {
  width: 100vw;
  overflow-x: hidden;
}



