:root {
  --maincolor: #744f81;
  --bgcolor: #f3f2f0;
}

.home {
  background-color: var(--bgcolor);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f3f2f0));
  background-image: -webkit-linear-gradient(#fff, #f3f2f0);
  background-image: -o-linear-gradient(#fff, #f3f2f0);
  background-image: linear-gradient(#fff, #f3f2f0);
}
.home .container > .row {
  min-height: 100vh;
  -ms-flex-align: center;
      align-items: center;
}

.home .title {
  color: var(--maincolor);
  font-weight: 900;
  font-size: 90px;
  line-height: 80px;
  letter-spacing: -5px;
}

.home hr {
  width: 70px;
  margin-left: 0;
  border-width: 10px;
  border-color: var(--maincolor);
}
.home a {
  color: #333;
  text-decoration: none;
}
.home a:hover {
  color: #111;
  text-decoration: none;
}
.home .desc {
  color: #555;
  line-height: 34px;
  font-size: 18px;
}
.home .btn {
  padding: 10px 30px;
  margin-right: 10px;
  font-weight: 900;
  background-color: #744f81cf;
  font-size: 20px;
  border: none;
}
.home .btn:last-child {
  -webkit-transform: translateX(-10px);
      -ms-transform: translateX(-10px);
          transform: translateX(-10px);
  opacity: 0;
  -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  -o-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}
.home .btn:hover + .btn {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}
.home .btn:hover {
  background-color: var(--maincolor);
}
.home .img-profile {
  margin-bottom: 20px;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  border: 10px solid #eee;
  -webkit-box-shadow: 0 2px 7px rgba(0, 0, 0, 0.4);
          box-shadow: 0 2px 7px rgba(0, 0, 0, 0.4);
}

.home .box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: column;
      flex-flow: column;
  -webkit-transition: cubic-bezier(0.165, 0.84, 0.44, 1) 1s;
  -o-transition: cubic-bezier(0.165, 0.84, 0.44, 1) 1s;
  transition: cubic-bezier(0.165, 0.84, 0.44, 1) 1s;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  cursor: pointer;
  position: relative;
  color: #555;
}
.home .box a {
  text-align: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.home .box p {
  margin-top: 10px;
  font-weight: 900;
  font-size: 14px;
}
.home .box:hover {
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
          box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  background-color: #fefefe;
  z-index: 999;
}
.home .box i {
  font-size: 50px;
}

.home .box-container {
  -ms-flex-line-pack: center;
      align-content: center;
}
.home .box-container .box:first-child {
  border-bottom: 1px solid #ccc;
}

.home .project {
  border: 1px solid #ccc;
  border-top: none;
  border-bottom: none;
}
.home .project i {
  font-size: 80px;
}

@media (max-width: 992px) {
  .home {
    text-align: center;
  }
  .home .title {
    font-size: 60px;
    line-height: 60px;
  }
  .home hr {
    margin-left: auto;
    margin-right: auto;
  }
  .home a {
    margin: 0 auto;
    display: block;
  }
  .home .mail-container {
    display: block;
  }
  .home .box {
    margin: 20px 0;
  }
}



